.careerobj-bg {
    height: 300px;
    background: url("https://images.unsplash.com/photo-1605408882110-5a97cac2210a?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1000&q=80");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    /* background-position-y: 75%; */
    background-position: center;
    overflow: hidden;
}
.container {
    max-width: 960px;
    margin: 0 auto;
}
.careerobj-overlay{
    background: rgba(256, 256, 256, 0.8);
}
.careerobj-con {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
}
.careerobj-text {
    color: #000;
    text-align: center;
    margin: 10%;
}