.modal-modal, .modal-overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    /* margin-top: 5rem; */
    z-index: 1;
}

.modal-overlay {
    background: rgba(49,49,49,0.8);
    z-index: 0;
}
.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
}

.modal-body{
    max-height: 9rem;
    overflow-y: auto;
  }

.close-modal {
    position: absolute;
    top: 1px;
    right: 6px;
    /* padding: 5px 7px; */
    background: transparent;
    border: none !important;
}

.center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

