.card {
    overflow: hidden;
    box-shadow: 0 2px 20px #e1e5ee;
    border-radius: 0.2rem;;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: transform 200ms ease-in;
    height: 100%;
}
.card-img-top {
    max-width: 1280px;
    max-height: 720px;
}
.card:hover{
    transform: scale(1.02);
}
.card-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: #e1e5ee;
}
.card:hover .card-overlay {
    opacity: 0.8;
}

.card-text {
    white-space: nowrap;
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
}

  @media screen and (max-width: 480px) {
    .card {
        margin-left: 2rem;
        margin-right: 2rem;
    }
}
.hover-btn {
    padding-left: 3rem;
    padding-right: 3em;
    padding-top: 1rem;
    padding-bottom: 1rem;
    color: black;
    font-family: "Roboto Condensed" sans-serif;
    font-size: 1rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
  }
